.App {
  font-family: sans-serif;
  text-align: center;
}

body {
  height: auto;
}

/*  Dark styles for card */
.ui.dark.cards > .dark.card,
.ui.dark.card {
  max-width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  width: 290px;
  min-height: 0px;
  background: @dark-bg-40;
  padding: 0em;
  border: none;
  border-radius: 0.28571429rem;
  box-shadow: 0px 1px 3px 0px @dark-bg-40, 0px 0px 0px 1px @dark-bg-40;
  transition: box-shadow 0.1s ease, transform 0.1s ease;
  z-index: "";
}

.ui.dark.card {
  background: @dark-bg-40;
}

.ui.dark.cards > .dark.card > .content > a.header,
.ui.dark.card > .content > a.header {
  color: white;
}

.ui.cards > .card .meta,
.ui.card .meta {
  font-size: 1em;
  color: @grey-50;
}

.ui.dark.cards > .dark.card > .content > .header,
.ui.dark.card > .content > .header {
  /* //display: block;
  //margin: '';
  //font-family: 'Roboto', 'sans-serif'; */
  color: white;
}

.ui.dark.cards > .dark.card > .content > .description,
.ui.dark.card > .content > .description {
  clear: both;
  color: white;
}

.ui.dark.cards > .dark.card > .content > a.header:hover,
.ui.dark.card > .content > a.header:hover {
  color: rgba(0, 0, 0, 0.8);
}

.ui.dark.cards > .dark.card > .extra,
.ui.dark.card > .extra {
  border-top: 1px solid @textColor !important;
  color: inherit;
}
